export var activated = false;
var selected_device;
var devices = [];
export var zebraPrinter;

export function setup() {
    //Get the default device from the application as a first step. Discovery takes longer to complete.
    window.BrowserPrint.getDefaultDevice(
        "printer",
        function (device) {
            //Add device to list of devices and to html select element
            console.log("Add device");
            console.log(device);
            selected_device = device;
            zebraPrinter = new window.Zebra.Printer(device);
            devices.push(device);

            //Discover any other devices available to the application
            window.BrowserPrint.getLocalDevices(
                function (device_list) {
                    for (var i = 0; i < device_list.length; i++) {
                        //Add device to list of devices and to html select element
                        var device = device_list[i];
                        if (!selected_device || device.uid !== selected_device.uid) {
                            devices.push(device);
                        }
                    }
                },
                function () {
                    alert("Error getting local devices");
                },
                "printer"
            );
            activated = true;
        },
        function (error) {
            alert(error);
        }
    );
}

export function getStatus() {
    return zebraPrinter.getStatus();
}

export function getInfo() {
    return zebraPrinter.getInfo();
}

export function getConfig() {
    window.BrowserPrint.getApplicationConfiguration(
        function (config) {
            alert(JSON.stringify(config));
        },
        function (error) {
            alert(JSON.stringify(new window.BrowserPrint.ApplicationConfiguration()));
        }
    );
}

export function printConfig() {
    selected_device.send("~wc", undefined, errorCallback);
}

export function printTestLabel() {
    selected_device.send("^XA^FO200,200^A0N36,36^FDTest Label^FS^XZ", undefined, errorCallback);
}

export function writeToSelectedPrinter(dataToWrite) {
    selected_device.send(dataToWrite, undefined, errorCallback);
}

var readCallback = function (readData) {
    if (readData === undefined || readData === null || readData === "") {
        alert("No Response from Device");
    } else {
        alert(readData);
    }
};

var errorCallback = function (errorMessage) {
    alert("Error: " + errorMessage);
};

export function readFromSelectedPrinter() {
    selected_device.read(readCallback, errorCallback);
}

export function getDeviceCallback(deviceList) {
    alert("Devices: \n" + JSON.stringify(deviceList, null, 4));
}

export function sendImage(imageUrl) {
    selected_device.convertAndSendFile(imageUrl, undefined, errorCallback);
}

export function sendFile(fileUrl) {
    var url;
    url = window.location.href.substring(0, window.location.href.lastIndexOf("/"));
    url = url + "/" + fileUrl;
    selected_device.sendFile(url, undefined, errorCallback);
}
