import React from "react";
import StatisticsNumber from "./StatisticsNumber";
import StatisticsChart from "./StatisticsChart";
import StatisticsRight from "./StatisticsRight";
import { fetchStatistics } from "../../helper/api";

function Statistics() {
    const [statNumbers, setStatNumbers] = React.useState([localStorage.getItem("checkedInAttendees") || "-", localStorage.getItem("allAttendees") || "-"]);
    const [rights, setRights] = React.useState([]);

    const formattedRights = rights.map((item) => {
        return <StatisticsRight key={item.key} item={item.data} />;
    });

    React.useEffect(() => {
        async function fetch() {
            if (localStorage.getItem("webhookURL")) {
                const data = await fetchStatistics();
                setStatNumbers([data.checkedInAttendees, data.allAttendees]);
                localStorage.setItem("checkedInAttendees", data.checkedInAttendees);
                localStorage.setItem("allAttendees", data.allAttendees);
                setRights(data.rights);
            } else {
                console.log("Keine Webhook URL hinterlegt");
            }
        }
        fetch();
    }, []);

    return (
        <div className="row">
            <div className="container col-md-6 pb-2">
                <div className="row gx-2 gy-2">
                    <StatisticsNumber number={statNumbers[0]} description="Teilnehmer eingecheckt" />
                    <StatisticsNumber number={statNumbers[1]} description="Teilnehmer registriert" />
                    <StatisticsChart />
                </div>
            </div>
            <div className="container col-md-6">
                <div className="row gx-2 gy-2">{formattedRights}</div>
            </div>
        </div>
    );
}

export default Statistics;
