import axios from "axios";

const baseURL = "https://hook.doo.integromat.celonis.com/" + localStorage.getItem("webhookURL");

export function ticketValidation(hash) {
    return axios.get(`${baseURL}?request=ticketValidation&hash=${hash}`);
}

export function ticketCheckin(hash, identifier) {
    return axios.get(`${baseURL}?request=ticketCheckin&hash=${hash}&identifier=${identifier}`);
}

export function ticketAutoCheckin(hash) {
    return axios.get(`${baseURL}?request=ticketCheckin&hash=${hash}`);
}

export function defaultBadgePrinted(hash) {
    return axios.get(`${baseURL}?request=defaultBadgePrinted&hash=${hash}`);
}

export const fetchStatistics = async () => {
    let URL = "https://hook.doo.integromat.celonis.com/" + localStorage.getItem("webhookURL") + "?request=stats&identifier=" + localStorage.getItem("eid");
    const { data } = await axios.get(URL);
    console.log(data);
    return data;
};

export const searchTicket = async (input) => {
    let URL = "https://hook.doo.integromat.celonis.com/" + localStorage.getItem("webhookURL") + "?request=search&identifier=";
    const { data } = await axios.get(URL + input);
    console.log(data);
    return data;
};
