import React from "react";
import formatDate from "../../utils/formatDate";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faCheck, faTicket } from "@fortawesome/free-solid-svg-icons";
import useCheckin from "../../helper/useCheckin";

function HomeCheckinTile(props) {
    const [data, loading, error, checkin] = useCheckin({ hash: props.attendee.ticketHash, identifier: props.item.TidPid });

    const count = countEntries();
    const rightValid = validateRight();

    function countEntries() {
        var count = 0;
        Object.entries(props.attendee.data.checkIns).forEach(([key, value]) => {
            if (value.ID === props.item.TidPid) {
                count++;
            }
        });
        return count;
    }

    function validateRight() {
        var valid = true;

        if (countEntries() >= props.item.maxEntries) {
            valid = false;
        }

        Object.entries(props.item.timeFrames).forEach(([key, value]) => {
            var from = new Date(value.validFrom);
            var till = new Date(value.validTill);
            if (!(from < Date.now() && Date.now() < till)) {
                valid = false;
            }
        });

        return valid;
    }

    return (
        <div id={props.item.TidPid} className={"col-12 col-sm-6 col-md-12 col-xl-6 " + (rightValid ? "" : "not-available")}>
            <div className={"tile p-3 w-100 text-center d-flex flex-column justify-content-between background--white"}>
                <div className="tile-icon-container">
                    <FontAwesomeIcon className="tile-icon" icon={faTicket} />
                </div>
                <p>
                    <a className="text text--primary" data-bs-toggle="collapse" href={"#collapseExample" + props.item.TidPid} role="button" aria-expanded="false" aria-controls={"collapseExample" + props.item.TidPid}>
                        <span>{props.item.Name}</span>
                    </a>
                </p>
                {(count > 0 || data?.status === "success") && (
                    <p className="text--green fw-bold">
                        <FontAwesomeIcon className="me-2" icon={faCheck} />
                        <span>Eingecheckt</span>
                    </p>
                )}
                {error && (
                    <p className="text--red fw-bold">
                        <span>error</span>
                    </p>
                )}
                <div className="collapse" id={"collapseExample" + props.item.TidPid}>
                    <div className="card card-body">
                        <div className="text text--sm text--secondary m-1">
                            <p className="text text--md text--primary mb-0 mx-1 ">Zeitraum</p>
                            {props.item.timeFrames.map((item, index) => {
                                return (
                                    <p key={"c" + index} className="text text--sm text--secondary m-0">
                                        {formatDate(item.validFrom)} - {formatDate(item.validTill)}
                                    </p>
                                );
                            })}
                            <p className="text text--md text--primary mb-0 mt-2 mx-1 ">Eintritte</p>
                            <p className="text text--sm text--secondary m-0">
                                {count}/{props.item.maxEntries}
                            </p>
                        </div>
                    </div>
                </div>
                <p className="warning-text text text--sm text--secondary mb-0"></p>
                {rightValid && (
                    <button onClick={() => checkin(props.item.TidPid)} className="btn btn-primary mt-1">
                        {loading ? <span className="spinner-border spinner-border-sm" role="status" aria-hidden="true"></span> : <span> {count > 0 ? "Erneut einchecken" : "Ticket entwerten"}</span>}
                    </button>
                )}
            </div>
        </div>
    );
}

export default HomeCheckinTile;
