import React from "react";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faCheck, faPrint } from "@fortawesome/free-solid-svg-icons";
import * as Zebra from "../../print/zebra";
import * as api from "../../helper/api";

function HomePrintTile(props) {
    function printBadge() {
        console.log("Print: " + props.item.url);
        Zebra.zebraPrinter
            .isPrinterReady()
            .then(() => {
                Zebra.sendImage(props.item.url);
                api.defaultBadgePrinted(props.item.hash)
                    .then((response) => console.log(response))
                    .catch((err) => console.log(err));
            })
            .catch(function (error) {
                console.error(error);
            });
    }

    return (
        <div className="col-12 col-sm-6 col-md-12 col-xl-6">
            <div className="tile background--white p-3 w-100 text-center d-flex flex-column justify-content-between">
                <div className={"tile-icon-container " + (props.item.printed ? "background--green" : "")}>
                    <FontAwesomeIcon className="tile-icon" icon={faPrint} />
                </div>
                <p>
                    <a className="text text--primary" data-bs-toggle="collapse" href={"#collapse" + props.id} role="button" aria-expanded="false" aria-controls={"collapse" + props.id}>
                        <i className="fa fa-print text--colored"></i>
                        <span>{props.item.name}</span>
                    </a>
                </p>
                {props.item.printed && (
                    <p className="text--green fw-bold">
                        <FontAwesomeIcon className="me-2" icon={faCheck} />
                        <span>Bereits gedruckt</span>
                    </p>
                )}
                <div className="collapse" id={"collapse" + props.id}>
                    <div className="card card-body">
                        <img className="img-fluid" src={props.item.url} alt="" />
                        <p className="text text--sm text--secondary m-0">
                            <a className="link" id="badge-url" target="_blank" rel="noreferrer" href={props.item.url}>
                                Badge
                            </a>
                        </p>
                    </div>
                </div>
                <button type={props.item.type} onClick={() => printBadge()} className="btn btn-primary mt-1">
                    {props.item.printed ? "Erneut drucken" : "Badge drucken"}
                </button>
            </div>
        </div>
    );
}

export default HomePrintTile;
