import React, { useState } from "react";
import * as Zebra from "../../print/zebra";

function SettingsZebra() {
    const [printerInfo, setPrinterInfo] = useState({});
    const [printerStatus, setPrinterStatus] = useState();

    React.useEffect(() => {
        refresh();
    }, []);

    async function refresh() {
        const info = await Zebra.getInfo();
        const status = await Zebra.getStatus();

        setPrinterInfo(info);
        setPrinterStatus(status.getMessage());
    }

    return (
        <div className="col-12">
            <div className="tile background--white p-3 px-md-5">
                <div className="text text-center">
                    <p className="text--md ">Druckereinstellungen</p>
                    <p className="text--secondary text--sm">Erklärung Druckereinstellungen</p>
                </div>

                <div className="d-flex justify-content-between">
                    <p className="p-0 m-0">
                        <a className="text text--primary" data-bs-toggle="collapse" href="#printerCollapse" role="button" aria-expanded="false" aria-controls="printerCollapse">
                            <span>{printerInfo.model}</span>
                        </a>
                    </p>
                    {printerStatus === "Ready" ? <p className="p-0 m-0 text--green">{printerStatus}</p> : <p className="p-0 m-0 text--red">{printerStatus}</p>}
                </div>
                <div className="collapse" id="printerCollapse">
                    <div className="card card-body">
                        <div className="d-flex justify-content-between">
                            <button onClick={() => Zebra.printConfig()} className="btn btn-sm btn-outline-primary m-1">
                                Konfigutation
                            </button>
                            <button onClick={() => Zebra.printTestLabel()} className="btn btn-sm btn-outline-primary m-1">
                                Testlabel
                            </button>
                            <button onClick={() => refresh()} className="btn btn-sm btn-outline-primary m-1">
                                Aktualisieren
                            </button>
                        </div>
                        <div className="text text--sm text--secondary m-1 mt-3">Firmware: {printerInfo.firmware}</div>
                    </div>
                </div>
            </div>
        </div>
    );
}

export default SettingsZebra;
