import React from "react";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faUser } from "@fortawesome/free-solid-svg-icons";
import formatDate from "../../utils/formatDate";

function HomeAttendeeInfo(props) {
    const formattedLogs = props.attendee.logs.map((item, index) => {
        return (
            <p key={index}>
                {formatDate(item.date)}: {item.description}
            </p>
        );
    });

    return (
        <div className="col-12">
            <div className="tile background--white p-3 p-md-4">
                {props.loaderActivated && (
                    <div className="text-center">
                        <div className="spinner-border text-center" role="status">
                            <span className="sr-only">Loading...</span>
                        </div>
                    </div>
                )}
                {!props.loaderActivated && (
                    <div>
                        <h4 className="text--primary">
                            <FontAwesomeIcon className="text--colored me-2" icon={faUser} />
                            <span id="name">
                                {props.attendee.firstName} {props.attendee.lastName}
                            </span>
                        </h4>
                        <div className="ps-2 ms-2 border-grey-left">
                            <div className="check-in__booking-id">
                                <p className="text text--sm text--secondary mb-0">
                                    <span>Ticket-ID</span>
                                </p>
                                <p id="booking-id" className="text text--md text--primary me-0 mb-3">
                                    {props.ticketHash}
                                </p>
                            </div>
                            <div className="check-in__booking-id">
                                <p className="text text--sm text--secondary mb-0">
                                    <span>Buchungsnummer</span>
                                </p>
                                <p id="booking-id" className="text text--md text--primary me-0 mb-3">
                                    {props.attendee.bookingNumber}
                                </p>
                            </div>
                            <div className="check-in__booking-event">
                                <p className="text text--sm text--secondary mb-0">
                                    <span>Veranstaltung</span>
                                </p>
                                <p id="event" className="text text--md text--primary me-0 mb-3">
                                    {props.attendee.event}
                                </p>
                            </div>
                        </div>
                        <p className="m-2">
                            <a className="text--colored" data-bs-toggle="collapse" href="#logs" role="button" aria-expanded="false" aria-controls="logs">
                                Logs ansehen
                            </a>
                        </p>
                        <div className="collapse" id="logs">
                            <div className="card card-body" id="logs-body">
                                {formattedLogs}
                            </div>
                        </div>
                    </div>
                )}
            </div>
        </div>
    );
}

export default HomeAttendeeInfo;
