import React from "react";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faSearch } from "@fortawesome/free-solid-svg-icons";
//import { QrReader } from "react-qr-reader";
import { searchTicket } from "../../helper/api";

function HomeTicketInput(props) {
    const [searchInput, setSearchInput] = React.useState("");
    const [searchResults, setSearchResults] = React.useState([]);

    const formattedSearchResults = searchResults.map((item) => {
        return (
            <li
                key={item.key}
                onClick={(e) => {
                    props.processInput(item.key);
                    resetSearchResults();
                }}
            >
                {item.key} - {item.data.firstName} {item.data.lastName}
            </li>
        );
    });

    async function search(e) {
        if (!e.target.value.startsWith("/") && e.target.value.length >= 3 && localStorage.getItem("webhookURL")) {
            console.log("Suche: " + e.target.value);
            const data = await searchTicket(e.target.value);
            data.results[0].key ? setSearchResults(data.results) : setSearchResults([]);
        } else {
            setSearchResults([]);
        }
    }

    function resetSearchResults() {
        setSearchResults([]);
        try {
            document.getElementById("searchHomeTicketInput").value = "";
        } catch {
            console.log("Couldn't reset search Input");
        }
    }

    /*
    function onQrResult(result, error) {
        if (!!result && lastQrScan !== result) {
            console.log("New Scan: " + result?.text);
            props.processInput(result?.text);
            lastQrScan = result;
        }

        if (!!error) {
            console.info(error);
        }
    }*/

    return (
        <div className="col-12 text-center">
            <div className="tile background--white p-3 p-lg-4">
                {JSON.parse(localStorage.getItem("searchActivated")) && (
                    <div>
                        <div className="searchInput mb-0">
                            <input
                                onKeyUp={(e) => search(e)}
                                onChange={(event) => {
                                    setSearchInput(event.target.value);
                                }}
                                className="form-control me-2"
                                id="searchHomeTicketInput"
                                type="text"
                                placeholder="Suche..."
                                name="search"
                            ></input>
                            <button
                                onClick={() => {
                                    props.processInput(searchInput);
                                    resetSearchResults();
                                }}
                                className="btn btn-primary"
                                id="searchTicket"
                            >
                                <FontAwesomeIcon icon={faSearch} />
                            </button>
                        </div>
                        <div id="result" className="m-0 pt-1">
                            <ul id="result-list">{formattedSearchResults}</ul>
                        </div>
                    </div>
                )}

                <h4 id="warning-text-container" className="text--primary text-center p-4 d-none">
                    <span id="warning-text" className="text--red"></span>
                </h4>
            </div>
        </div>
    );
}

export default HomeTicketInput;
