import React from "react";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faCircleExclamation } from "@fortawesome/free-solid-svg-icons";

function Navbar(props) {
    return (
        <div className="navbar-container">
            <nav className="navbar">
                <div className="container-fluid p-0 m-0 navigation-links justify-content-around">
                    <button onClick={() => props.setContent("Home")} className={props.content === "Home" ? "navigation-link-active" : "navigation-link"}>
                        CHECKIN
                    </button>
                    <button onClick={() => props.setContent("Statistics")} className={props.content === "Statistics" ? "navigation-link-active" : "navigation-link"}>
                        STATISTIK
                    </button>
                    <button onClick={() => props.setContent("Settings")} className={props.content === "Settings" ? "navigation-link-active" : "navigation-link"}>
                        EINSTELLUNGEN
                        {props.settingsWarning && <FontAwesomeIcon size="sm" className="text-warning ms-1 text-sm my-auto" icon={faCircleExclamation} />}
                    </button>
                </div>
            </nav>
        </div>
    );
}

export default Navbar;
