import { useEffect, useState } from "react";
import axios from "axios";
import { initAttendee } from "../utils/initAttendee";

function useGetAttendee() {
    const [hash, setHash] = useState(null);
    const [attendee, setAttendee] = useState(initAttendee);
    const [loading, setLoading] = useState(false);
    const [error, setError] = useState(null);

    useEffect(() => {
        if (hash) {
            setLoading(true);
            console.log("Get attendee");
            var url = `https://hook.doo.integromat.celonis.com/${localStorage.getItem("webhookURL")}?request=getAttendee&hash=${hash}`;
            axios
                .get(url)
                .then((response) => {
                    setAttendee(response.data);
                })
                .catch((err) => {
                    setError(err);
                })
                .finally(() => {
                    setLoading(false);
                });
        }
    }, [hash]);

    return [hash, attendee, loading, error, setHash];
}

export default useGetAttendee;
