import React from "react";

function SettingsSlider(props) {
    const [isChecked, setChecked] = React.useState(JSON.parse(localStorage.getItem(props.id)) || false);

    function saveSetting(checked) {
        setChecked(!isChecked);
        localStorage.setItem(props.id, checked);
    }

    return (
        <div className="col-12 col-sm-6 col-md-6 col-xl-6">
            <div className="tile background--white text p-3 w-100 h-100 text-center d-flex flex-column justify-content-between align-items-center">
                <p className="text--md">{props.title}</p>
                <p className="text--secondary text--sm">{props.description}</p>
                <label className="switch">
                    <input id={props.id} type="checkbox" checked={isChecked} onChange={(e) => saveSetting(e.target.checked)}></input>
                    <span className="slider round"></span>
                </label>
            </div>
        </div>
    );
}

export default SettingsSlider;
