import React from "react";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faCircleCheck, faCircleExclamation, faCircleXmark } from "@fortawesome/free-solid-svg-icons";

function HomeWarning(props) {
    function getIcon() {
        if (props.icon === "success") {
            return <FontAwesomeIcon className="me-1 text-success" icon={faCircleCheck} />;
        } else if (props.icon === "warning") {
            return <FontAwesomeIcon className="me-1 text-warning" icon={faCircleExclamation} />;
        } else if (props.icon === "error") {
            return <FontAwesomeIcon className="me-1 text-danger" icon={faCircleXmark} />;
        }
    }

    return (
        <div className="col-12 position-relative">
            <button onClick={() => props.setWarning("")} className="btn-close position-absolute top-0 end-0 mt-2 me-3"></button>

            <div className="tile background--white p-3 p-md-4">
                <div className="d-flex align-items-center justify-content-center">
                    <div className="text--lg me-2">
                        {getIcon()}
                        {props.text}
                    </div>
                </div>
            </div>
        </div>
    );
}

export default HomeWarning;
