import React from "react";
import SettingsConnection from "./SettingsConnection.js";
import SettingsSlider from "./SettingsSlider.js";
import SettingsColor from "./SettingsColor.js";
import SettingsZebra from "./SettingsZebra";
import * as Zebra from "../../print/zebra";

function Settings(props) {
    const sliderInput = [
        { title: "QR Code Scanner", description: "Aktiviert den QR Code Scanner, um Tickets mit dem Smartphone einzuscannen.", id: "QrCodeActivated" },
        { title: "Suche", description: "Falls aktiviert, wird eine Suchleiste auf der Checkin Seite eingeblendet.", id: "searchActivated" },
        { title: "Auto Checkin", description: "Tickets werden nach dem Scannen direkt entwertet, falls diese gültig sind.", id: "autoCheckinActivated" },
        { title: "Auto Badging", description: "Falls ein Badge vorhanden ist, wird automatisch der Drucker angesteuert.", id: "autoBadgePrintActivated" },
    ];

    const sliders = sliderInput.map((item) => {
        return <SettingsSlider key={item.id} title={item.title} description={item.description} id={item.id} />;
    });

    return (
        <div className="row">
            <div className="container col-lg-6 pb-2">
                <div className="row gx-2 gy-2">
                    <SettingsConnection setSettingsWarning={props.setSettingsWarning} />
                    <SettingsColor />
                    {Zebra.activated && <SettingsZebra />}
                </div>
            </div>
            <div className="container col-lg-6">
                <div className="row gx-2 gy-2">{sliders}</div>
            </div>
        </div>
    );
}

export default Settings;
