import React from "react";
import { Chart as ChartJS, ArcElement, Tooltip, Legend } from "chart.js";
import { Doughnut } from "react-chartjs-2";

ChartJS.register(ArcElement, Tooltip, Legend);

function StatisticsChart() {
    const data = {
        labels: ["nicht eingecheckt", "eingecheckt"],
        datasets: [
            {
                backgroundColor: ["#D3D3D3", localStorage.getItem("primary-color") ? localStorage.getItem("primary-color") : "#006ab7"],
                data: [localStorage.getItem("allAttendees") - localStorage.getItem("checkedInAttendees"), localStorage.getItem("checkedInAttendees")],
            },
        ],
    };

    return (
        <div className="col-12">
            <div className="tile background--white p-4">
                <div className="doughnut-chart d-flex justify-content-center">
                    <Doughnut data={data} />
                </div>
            </div>
        </div>
    );
}

export default StatisticsChart;
