import { useState } from "react";
import axios from "axios";

function useGetAttendee(input) {
    const [data, setData] = useState(null);
    const [loading, setLoading] = useState(false);
    const [error, setError] = useState(null);

    function checkin() {
        setLoading(true);
        console.log("Checkin attendee");
        var url = `https://hook.doo.integromat.celonis.com/${localStorage.getItem("webhookURL")}?request=checkinAttendee&hash=${input.hash}&identifier=${input.identifier}`;
        axios
            .get(url)
            .then((response) => {
                setData(response.data);
                console.log(response.data);
            })
            .catch((err) => {
                setError(err);
                console.log(err);
            })
            .finally(() => {
                setLoading(false);
            });
    }

    return [data, loading, error, checkin];
}

export default useGetAttendee;
