import React from "react";

function SettingsColor() {
    const [primaryColor, setPrimaryColor] = React.useState(localStorage.getItem("primary-color") || "#006ab7");
    const [themeColor, setThemeColor] = React.useState(localStorage.getItem("theme-color") || "#006ab7");
    const [backgroundColor1, setBackgroundColor1] = React.useState(localStorage.getItem("background-color-1") || "#006ab7");
    const [backgroundColor2, setBackgroundColor2] = React.useState(localStorage.getItem("background-color-2") || "#006ab7");

    function saveColor(e) {
        localStorage.setItem(e.target.id, e.target.value);
        if (e.target.id === "primary-color") {
            setPrimaryColor(e.target.value);
            document.documentElement.style.setProperty("--primary-color", localStorage.getItem("primary-color"));
        } else if (e.target.id === "theme-color") {
            setThemeColor(e.target.value);
            document.querySelector('meta[name="theme-color"]').setAttribute("content", localStorage.getItem("theme-color"));
        } else if (e.target.id === "background-color-1") {
            setBackgroundColor1(e.target.value);
            document.documentElement.style.setProperty("--background-color-1", localStorage.getItem("background-color-1"));
        } else if (e.target.id === "background-color-2") {
            setBackgroundColor2(e.target.value);
            document.documentElement.style.setProperty("--background-color-2", localStorage.getItem("background-color-2"));
        }
    }

    return (
        <div className="col-12">
            <div className="tile background--white p-3 px-md-5 text-center">
                <p className="text text--md mb-3"> Farbschema</p>
                <p className="text text--secondary text--sm">Hinterlege Farben deiner Wahl.</p>
                <div className="d-flex justify-content-around flex-wrap">
                    <div className="form-group text-start me-2 my-2 text-center">
                        <label className="mb-0 text text--secondary text--sm">Highlights</label>
                        <input type="color" className="form-control" id="primary-color" value={primaryColor} onChange={(e) => saveColor(e)} />
                    </div>
                    <div className="form-group text-start mx-2 my-2 text-center">
                        <label className="mb-0 text text--secondary text--sm">Header</label>
                        <input type="color" className="form-control form-control text--sm" id="theme-color" value={themeColor} onChange={(e) => saveColor(e)} />
                    </div>
                    <div className="form-group text-start mx-2 my-2 text-center">
                        <label className="mb-0 text text--secondary text--sm">Background 1</label>
                        <input type="color" className="form-control form-control text--sm" id="background-color-1" value={backgroundColor1} onChange={(e) => saveColor(e)} />
                    </div>
                    <div className="form-group text-start ms-2 my-2 text-center">
                        <label className="mb-0 text text--secondary text--sm">Background 2</label>
                        <input type="color" className="form-control form-control text--sm" id="background-color-2" value={backgroundColor2} onChange={(e) => saveColor(e)} />
                    </div>
                </div>
            </div>
        </div>
    );
}

export default SettingsColor;
